<template>
  <div class="chooseImage_out" v-if="show" @click.stop.prevent="close">
    <div class="chooseImage_inner" @click.stop='open'>
      <label for='cameraRef' @click='addSgm(0)'>
        <input id="cameraRef" @change="takeImage('cameraRef',0)" ref="cameraRef" type="file" accept="image/*" capture="camera" class="hidden" />
      </label>
      <div class="chooseImage_dividingLine"></div>
      <label for="imageRef" @click='addSgm(1)'>
        <input id="imageRef" ref='imageRef' class="hidden" @change="takeImage('imageRef',1)" type="file" accept="image/*"/>
      </label>
      <div class="chooseImage_dividingLine"></div>
      <div @click.stop.prevent='addSgm(2)'></div>
      <!-- <img style="max-width: 100%;" :src="testImg"/> -->
    </div>
  </div>
</template>

<script>
  import Compressor from 'compressorjs';
  import lrz from 'lrz' 
  import {Toast} from 'vant';
  import 'vant/lib/toast/style';
  import bridge from 'fetch/bridge.js'
  export default{
    name:'uploadImage',
    props:{
      uploadIndex:{
        type:Number,
        default(){
          return 0
        }
      },
    },
    data(){
      return {
        show:false,
        imageParams:{
          maxQuality:1,
          minQuality:0.8,
          width:750,
          maxSize:1024*1024/2,
        },
        uploadType:'',
        tempCarType:'',
        testImg:'',
        addSgmTimer:null,
        writeIndex:-1,
      }
    },
    methods:{
      authAction(eventName){//
        let type=this.$route.query.type;
        console.log('type=',type)
        if(this.$route.query.test==1 || type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('--',eventName)
          }
        }
      },
      addSgm(num){
        let type=this.$route.query.type;
        console.log('type=',type,'num==',num)
        if(num==2 && type!='app'){
          this.close()
          return
        }
        clearTimeout(this.addSgmTimer);
        this.addSgmTimer=setTimeout(()=>{
          let uploadIndex=this.uploadIndex;
          let path=this.$route.path;
          if(path=="/addCar"){
            if(uploadIndex==3){
              console.log('num===',num,'uploadIndex==',uploadIndex)
              if(num==0){
                this.authAction('mycar_mycar_add_license_photo')
              }else if(num==1){
                this.authAction('mycar_mycar_add_license_photo_album')
              }else if(num==2){
                this.close()
                this.authAction('mycar_mycar_add_license_cancle')
              }
            }else if(uploadIndex==4){
              if(num==0){
                this.authAction('mycar_mycar_add_authorization_photo')
              }else if(num==1){
                this.authAction('mycar_mycar_add_authorization_photo_album')
              }else if(num==2){
                this.close()
                this.authAction('mycar_mycar_add_authorization_cancle')
              }
            }
           }else{
             console.log('=====')
             if(num==2){
               this.close()
             }else{
               if(!this.$route.path=='/writeMes'){
                 this.open('eeeeee')
               }
             }
           }
           clearTimeout(this.addSgmTimer);
        },20)
      },
      open(uploadType,tempCarType){
        this.show=true;
        console.log('======',uploadType)
        // if(uploadType!=undefined){
        //   this.uploadType=uploadType
        // }
        // if(!tempCarType){
        //   this.tempCarType=tempCarType
        // }
        // console.log('',uploadType,tempCarType)
      },
      savedata(tempCarType,uploadType,writeIndex){
        if(tempCarType!=undefined){
          this.tempCarType=tempCarType
        }
        if(uploadType!=undefined){
          this.uploadType=uploadType
        }
        if(writeIndex!=undefined){
          this.writeIndex=writeIndex;
        }
        console.log('',tempCarType,this.tempCarType,writeIndex)
      },
      close(){
        this.show=false;
      },
      chooseImage(refName){
        console.log('refName=',refName,this.$refs[refName])
        this.$refs[refName].click()
      },
      chooseImgShow(){
        
      },
      async takeImage(refName,sgmIndex){
        console.log('refName=',refName,this.$refs[refName],this.$refs)
        let file=this.$refs[refName].files[0];
        //this.close()
        console.log(file)
        // this.addSgm(sgmIndex);
        await this.asyncBeforeRead(file);//
        let path=this.$route.path;
        let newImg=null;
        console.log('path=====',path)
        if((this.writeIndex==1 && path=='/writeMes') || (this.writeIndex==3 || this.writeIndex==4)){
          console.log('5m000')
          console.log(file.size/1024/1024,file.size)
          if(file.size/1024/1024<5){
            return this.callImage(file)
          }else{
            this.imageParams.maxSize=1024*1024*5;
            //newImg = await this.compressorImage(file, 1);//
          }
        }else{
          this.imageParams.maxSize=1024*1024/2;
        }
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = (eve) => {
          let img = new Image();
          img.src = eve.target.result;
          img.onload = async () => {
            let width=img.width;
            this.imageParams.width=parseInt(width/2)
            console.log('img==00==',{img,width,eve,size:file.size})
            newImg = await this.compressorImage(file, 1);//
            this.callImage(newImg)
          };
        };
        //let newImg = await this.compressorImage(file, 1);//
        // this.localImage(newImg)
        // this.close()
        // console.log('==newImg=',{newImg})
      },
      callImage(newImg){
        this.localImage(newImg)
        this.close()
        console.log('==newImg=',{newImg})
      },
      localImage(file){
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = (eve) => {
          let img = new Image();
          img.src = eve.target.result;
          
          img.onload = () => {
            //this.imgSrc = img.src;
            //this.testImg=img.src;
            console.log('uploadType000==',this.uploadType,'=',file.size,file,file.name,'width=',img.width)
            this.close()
            this.$emit('localImage',{file,isLocal:true,img:img.src,uploadIndex:this.uploadIndex,uploadType:this.uploadType,tempCarType:this.tempCarType,writeIndex:this.writeIndex})
          };
        };
      },
      //
      asyncBeforeRead(file) {
        return new Promise((resolve, reject) => {
          var reg = /image\/(png|jpg|gif|jpeg|bmp)$/;
          console.log('',{file,type:file.type,name:file.name},reg.test(file.type))
          if (!reg.test(file.type)) {
            this.toast('')
            reject('');
          } else {
            resolve(file)
          }
        });
      },
      compressorImage(image, quality){//
        return new Promise((resolve, reject) => {
          return this.compressJs(image, quality,resolve, reject)
        })
      },
      compressJs(image, quality,resolve,reject){//
        let that=this;
        let {maxQuality,minQuality,width,maxSize}=this.imageParams;//
        if(!quality){
          quality=maxQuality
        }
        if(quality<minQuality){
          this.$Toast('')
          return reject('')
        }
        console.log('==',width,image.size>maxSize,image.size,maxSize)
        //if(image.size>maxSize){
          new Compressor(image, {
              quality:1,
              width,
              success:(result)=> {
                let file = new File([result], image.name, { type: image.type })
                if(image.name.indexOf('.jpeg')>=0){
                  console.log('jpeg',file.size)
                  file = new File([result], new Date().getTime()+'.jpg', { type: image.type })
                }
               // let file = new File([result], new Date().getTime()+'.jpg', { type: image.type })
               // return resolve(file)
               console.log('file.size>maxSize===',file.size>maxSize,file.size,maxSize,{result})
                if(file.size>maxSize){
                  if(minQuality===1){
                    this.toast('')
                    return reject('')
                  }
                  this.imageParams.width=this.imageParams.width-100;
                  return that.compressJs(file,1,resolve,reject)
                }else{
                  this.imageParams.width=750;
                  return resolve(file)
                }
              },
              error(err){
                console.log('---->>>>>',err)
                this.toast('')
                reject(err)
              }
          })
        // }else{
        //   return resolve(image)
        // }
        
      },
      toast(message,type='html'){//toast
        Toast({
          type,
          message,
          forbidClick: true,
        });
      },
    }
  }
</script>

<style scoped lang='less'>
  .chooseImage_out{
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    left: 0;
    z-index:11;
    input{
      height:0;
      width:0;
    }
    .chooseImage_inner{
      background: #fff;
      border-radius: 32px 32px 0px 0px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      >label,>div{
        display:block;
        font-size: 32px;
        font-weight: 500;
        text-align: center;
        color: #303030;
        line-height: 128px;
      }
      >div:last-child {
        margin-bottom: 32px;
      }
      > .checkedImg {
        background: #e5e5e5;
      }
      .chooseImage_dividingLine {
        border-top:1px solid rgba(215,215,215,.5);
        margin: 0 48px;
      }
    }
    .hiddenInput{
      height:0;
      width:0;
    }
  }
</style>
